import { DemoData } from './../demo';
import { Router } from '@angular/router';
import { CtrlrConfTab } from '../model/model-ctrlr-conf';
import { CheckBoxRendererComponent } from './../services/checkbox-renderer.component';
import { MsgBoxObj, CentreMsgObject, Selected } from 'open-plant-ng';
import { ButtonRendererComponent } from './../services/button-renderer.component';
import { CtrlrConfVw, CtrlrConf } from '../model/model-ctrlr-conf';
import { CtrlrTempl } from '../model/model-ctrlr-templ';
import { DataService } from './../data.service';
import { Component, OnInit, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-conf-ctrlr-edit-new-ctrlr',
  templateUrl: './conf-ctrlr-edit-new.component.html',
  styleUrls: ['./conf-ctrlr-edit-new.component.css']
})
export class ConfCtrlrEditNewComponent implements OnInit {

  constructor(private router: Router, private dataService: DataService) { }
  ctrlrTempls: CtrlrTempl[];
  newCtrlrTagName: string = "";
  newCtrlTemplId: string = "";


  ngOnInit() {
    if (!DemoData.initialized) DemoData.initialize();
    this.ctrlrTempls = DemoData.getCtrlrTempls();
    console.log(this.ctrlrTempls);
  }



  newNextClick() {
    let newTempl = this.ctrlrTempls.find(cT => cT.id == this.newCtrlTemplId);
    let temp = new CtrlrConf(null, this.newCtrlrTagName, "", newTempl, null);
    DemoData.ctrlrConfs = DemoData.ctrlrConfs.concat(temp);
    this.router.navigateByUrl("/configure-controller/" + temp.ctrlrTagName);
  }



  centreMsg: CentreMsgObject;
  onCMReady(param) {
    this.centreMsg = param;
  }

  msgBox: MsgBoxObj;
  onMBReady(param) {
    this.msgBox = param;
  }



}
