import { Utility } from './../utility';
import { DataService } from './../data.service';
import { ButtonRendererComponent } from './../services/button-renderer.component';
import { CtrlrCellRendererComponent } from './../services/ctrlr-cell-renderer.component';
import { CtrlrConfTab, CtrlrConfRowVw, CtrlrConfVw, CtrlrConfRow, CtrlrConfRowCol } from '../model/model-ctrlr-conf';
import { CtrlrTempl, CtrlrTemplParam } from '../model/model-ctrlr-templ';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MsgBoxObj, CentreMsgObject, Selected } from 'open-plant-ng';
@Component({
  selector: 'app-conf-ctrlr-edit-grid',
  templateUrl: './conf-ctrlr-edit-grid.component.html',
  styleUrls: ['./conf-ctrlr-edit-grid.component.css']
})



export class ConfCtrlrEditGridComponent implements OnInit {
  @Input() ctrlrConfTab: CtrlrConfTab;
  @Input() centreMsg: CentreMsgObject;
  @Input() msgBox: MsgBoxObj;
  @Output() msgBoxChange: EventEmitter<MsgBoxObj> = new EventEmitter<MsgBoxObj>();
  @Input() saveInitiated!: EventEmitter<void>;
  @Output() modelUpdated = new EventEmitter<CtrlrConfTab>();
  columnDefs = [];
  constructor(private dataService: DataService) { }
  ctrlrConfRowVws: CtrlrConfRowVw[]; //This is the rowData Object
  gridApi;
  columnApi
  ngOnInit() {
    if (this.saveInitiated) {
      this.saveInitiated.subscribe(data => {
        let updatedCtrlrConfRows: CtrlrConfRow[] = [];
        let headers: string[] = this.ctrlrConfTab.getColHeaders();
        this.ctrlrConfRowVws.forEach(rowVw => {
          let cols: CtrlrConfRowCol[] = [];
          for (n = 0; n < headers.length; n++)  cols = cols.concat(rowVw.getCtrlrConfRowCol(n));
          let updatedCtrlrConfRow = new CtrlrConfRow(rowVw.id, cols)
          updatedCtrlrConfRows = updatedCtrlrConfRows.concat(updatedCtrlrConfRow)
        });
        let updatedCtrlrConfTab: CtrlrConfTab = new CtrlrConfTab(this.ctrlrConfTab.tabName, this.ctrlrConfTab.tabId, this.ctrlrConfTab.ctrlrTempl, updatedCtrlrConfRows);

        this.modelUpdated.emit(updatedCtrlrConfTab);
      });
    }

    this.columnDefs = [];
    let n: number = 0;
    this.ctrlrConfRowVws = this.ctrlrConfTab.getCtrlrConfRowVws();
    this.columnDefs = this.columnDefs.concat({
      headerName: '', cellRendererFramework: ButtonRendererComponent, width: 30, cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        onClick: this.deleteBtnClick.bind(this),
        label: '',
        isVisible: true,
        iconClass: 'fa fa-trash',
        btnClass: 'btn btn-outline-primary border-0 py-0 px-1 '
      }
    });
    this.ctrlrConfTab.getColHeaders().forEach(h => {
      let newCol = {
        headerName: this.ctrlrConfTab.getColHeadersDesc()[n], field: 'c' + n, resizable: true, cellRendererFramework: CtrlrCellRendererComponent,
        headerClass: "op-header-center",
        cellRendererParams: {
          onClick: this.onSelectOPTagClick.bind(this),
        },
        width: 245,
      }
      switch (h) {
        case "Number": newCol.width = 60; break;
      }


      this.columnDefs = this.columnDefs.concat(newCol);
      n++;
    });

  }

  onSelectOPTagClick(params) {
    //console.log(params);
  }

  onGridReady(params) {
    this.gridApi = params.api;
    this.columnApi = params.columnApi;
  }

  newClick() {
    this.msgBox.showTextInput("New " + this.ctrlrConfTab.tabName, "Tag Name: ", (sel, tagName) => {
      if (sel == Selected.Ok) {
        let tabCtrlrTemplParams: CtrlrTemplParam[] = this.ctrlrConfTab.ctrlrTempl.getParamsOfTabId(this.ctrlrConfTab.tabId);
        let newRowM: CtrlrConfRow = Utility.generateNewCtrlrConfRow(this.ctrlrConfRowVws.length + 1, tagName, tabCtrlrTemplParams);
        let newRowVw: CtrlrConfRowVw = this.ctrlrConfTab.genCtrlrConfRowVw(newRowM);
        this.ctrlrConfRowVws = this.ctrlrConfRowVws.concat(newRowVw);
      } else {
        this.msgBox.hide();
      }
    });

  }


  deleteBtnClick(e) {
    let ctrlrConfRowVw: CtrlrConfRowVw = e.rowData;
    this.ctrlrConfRowVws = this.ctrlrConfRowVws.filter(r => r.id != ctrlrConfRowVw.id);
  }

}

