import { MsgBoxObj, CentreMsgObject } from 'open-plant-ng';
import { DemoData } from './../demo';
import { ButtonRendererComponent } from './../services/button-renderer.component';
import { Mapping } from './../model/model-ctrlr-templ';
import { CtrlrConf, CtrlrConfVw } from './../model/model-ctrlr-conf';
import { DataService } from './../data.service';
import { Global, FetchItem, OPFetch } from './../global';
import { Component, OnInit } from '@angular/core';

import { CtrlrHomeVw } from '../model/model-ctrlr';
import { timer } from 'rxjs';
import { AhrefRendererComponent } from '../services/ahref-renderer.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private dataService: DataService) { }

  gridApi;
  ctrlrConfs: CtrlrConf[];
  oPFetch: OPFetch = new OPFetch();
  msgBox: MsgBoxObj;
  cmObj: CentreMsgObject;

  columnDefs = [
    {
      headerName: 'Controller', cellRendererFramework: AhrefRendererComponent, width: 110, resizable: true,
      cellStyle: { 'display': 'flex', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        routerLinkText: function (object: CtrlrHomeVw) {
          return "/controllers/" + object.ctrlrTagName;
        },
        hrefText: function (object: CtrlrHomeVw) {
          return object.ctrlrTagName;
        },
        isEnabled: true,
        isVisible: true,
        aStyle: { 'font-weight': 'bold', 'color': 'blue' }
      }
    },
    {
      headerName: 'Description', field: 'description', resizable: true, sortable: true, cellStyle: { 'display': 'flex', 'align-items': 'center' }, flex: 3,
    },
    {
      headerName: 'Mode', field: 'mode', resizable: true, sortable: true, flex: 1,
      cellStyle: function (params) {
        let ctrlrHomeVw: CtrlrHomeVw = params.data;
        if (ctrlrHomeVw.modeMapping != null) {
          let hexColor: string = ctrlrHomeVw.modeMapping.getValueColor(ctrlrHomeVw.modeNumber);
          return { 'display': 'flex', 'align-items': 'center', 'color': hexColor };
        }
        else return { 'display': 'flex', 'align-items': 'center' };
      }
    },
    {
      headerName: 'Status', field: 'status', resizable: true, sortable: true, flex: 1,
      cellStyle: function (params) {
        let ctrlrHomeVw: CtrlrHomeVw = params.data;
        if (ctrlrHomeVw.statusMapping != null) {
          let hexColor: string = ctrlrHomeVw.statusMapping.getValueColor(ctrlrHomeVw.statusNumber);
          return { 'display': 'flex', 'align-items': 'center', 'color': hexColor };
        }
        else return { 'display': 'flex', 'align-items': 'center' };
      }
    },
    {
      headerName: 'CVs', cellRendererFramework: AhrefRendererComponent, width: 110, resizable: true, flex: 1, sortable: true,
      cellStyle: { 'display': 'flex', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        routerLinkText: function (object) {
          return "/controllers/" + object.ctrlrTagName + "/cv";
        },
        hrefText: function (object) {
          return object.nCV;
        },
        isEnabled: true,
        isVisible: true,
        aStyle: { 'font-weight': 'bold', 'color': 'blue' }
      }
    },
    {
      headerName: 'MVs', cellRendererFramework: AhrefRendererComponent, width: 110, resizable: true, flex: 1, sortable: true,
      cellStyle: { 'display': 'flex', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        routerLinkText: function (object) {
          return "/controllers/" + object.ctrlrTagName + "/mv";
        },
        hrefText: function (object) {
          return object.nCV;
        },
        isEnabled: true,
        isVisible: true,
        aStyle: { 'font-weight': 'bold', 'color': 'blue' }
      }
    },
    {
      headerName: 'DVs', cellRendererFramework: AhrefRendererComponent, width: 110, resizable: true, flex: 1, sortable: true,
      cellStyle: { 'display': 'flex', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        routerLinkText: function (object) {
          return "/controllers/" + object.ctrlrTagName + "/dv";
        },
        hrefText: function (object) {
          return object.nCV;
        },
        isEnabled: true,
        isVisible: true,
        aStyle: { 'font-weight': 'bold', 'color': 'blue' }
      }
    }
  ];
  ctrlrHomeVws: CtrlrHomeVw[] = [];


  ngOnInit() {
    if (!DemoData.initialized) DemoData.initialize();
  }


  //HOW UPDATE WORKS:
  //First, time is refreshed every 2 seconds below. 
  //In refreshData() sub, opFetch object which contains all the tags to fetch will perform the fetch itself
  //ctlrHomeVw runs ctlrHomeVw.update(oPFetch) to update the model with new data
  //oPFetch is given the list of TagIds to fetch during the start during refresh(), and subsequently in the CtrlrHomeVw Constructor
  onGridReady(event) {
    this.gridApi = event.api;
    this.refresh();
    const source = timer(0, 2000);
    source.subscribe(val => {
      this.refreshData();

    });
  }


  onCMReady(cmObj) {
    this.cmObj = cmObj;
  }

  onMBReady(msgBox) {
    this.msgBox = msgBox;
  }
  refresh() {
    this.ctrlrHomeVws = [];

    //Build Tag List
    let ctrlrConfs: CtrlrConf[] = this.dataService.getCtrlrConfs();
    ctrlrConfs.forEach(c => this.ctrlrHomeVws = this.ctrlrHomeVws.concat(new CtrlrHomeVw(c)));

    //Build Data Fetch
    this.oPFetch.clear();
    this.ctrlrHomeVws.forEach(c => this.oPFetch.add(c.statusTagId, "Status", false));
    this.ctrlrHomeVws.forEach(c => this.oPFetch.add(c.modeTagId, "Mode", false));
  }

  refreshData() {

    //Refresh Data Fetch
    this.oPFetch.refresh();
    this.ctrlrHomeVws.forEach(ctlrHomeVw => ctlrHomeVw.update(this.oPFetch));
    this.gridApi.refreshCells();
  }

  onRowClicked(event) {
    //console.log(event);
  }

  cVClick(e) {
    console.log(e);
  }

  mVClick(e) {
    console.log(e);
  }

  dVClick(e) {
    console.log(e);
  }
}