import { Global } from './../global';
import { DType, KPIGroup, CtrlrVarType, NumberFormat } from './enums'
//import { CtrlrTempl, CtrlrTemplParam } from './../model/model-ctrlr-templ';


//******* CONTROLLER TEMPLATES *******************************

export class CtrlrTempl { //eg Honeywell RMPCT, Aspentech DMC, ExaSMOC
    constructor(id: string, name: string, params: CtrlrTemplParam[], mappings: Mapping[]) {
        if (id == null) this.id = Global.newGuid(); else this.id = id;
        this.name = name;
        this.params = params;
        this.mappings = mappings;

    }
    id: string;
    name: string;
    params: CtrlrTemplParam[];
    showKPIGroup: boolean;
    mappings: Mapping[] = [];

    genCtrlrTemplConfVw(): CtrlrTemplVw {
        let tabs: CtrlrTemplTabVw[] = [];
        for (var n = 0; n <= 4; n++) {
            let ctrlrTemplParams: CtrlrTemplParam[] = [];
            this.params.forEach(r => {
                if (r.ctrlrTemplTabId == n) {
                    ctrlrTemplParams = ctrlrTemplParams.concat(new CtrlrTemplParam(r.name, r.desc, r.dType, r.canUseOPTag, r.canEdit, r.kPIGroup,
                        r.ctrlrTemplTabId, r.canEditTempl, r.cssClass, r.viewHeader, r.viewHeaderGr, r.mapping, r.numberFormat));
                }
            });
            tabs = tabs.concat(new CtrlrTemplTabVw(Global.ctrlrConfTabNames[n], (n == 1 || n == 2), this, ctrlrTemplParams, n));
        }

        let cT = new CtrlrTemplVw(this.id, this.name, tabs, this.mappings);
        return cT;
    }

    getParamsOfTabId(tabId: number): CtrlrTemplParam[] {
        let psT: CtrlrTemplParam[] = [];
        this.params.forEach(r => {
            if (r.ctrlrTemplTabId == tabId) {
                psT = psT.concat(new CtrlrTemplParam(r.name, r.desc, r.dType, r.canUseOPTag, r.canEdit, r.kPIGroup, r.ctrlrTemplTabId,
                    r.canEditTempl, r.cssClass, r.viewHeader, r.viewHeaderGr, r.mapping, r.numberFormat));
            }
        });
        return psT;
    }
}

export class MapPair {
    constructor(val: any, text: string, colorHex: string) {
        this.val = val;
        this.text = text;
        this.colorHex = colorHex;
        this.showCP = false;
    }
    val: any;
    text: string;
    colorHex: string;

    //Unused
    showCP: boolean;
}

export class Mapping {
    constructor(id: string = null, name: string, mapPairs: MapPair[], canDelete: boolean) {
        if (this.id == null) this.id = Global.newGuid(); else this.id = id;
        this.name = name;
        this.mapPairs = mapPairs;
        this.canDelete = canDelete;
    }
    id: string;
    name: string;
    mapPairs: MapPair[];
    canDelete: boolean;
    getValueText(val: any): string {
        let mp = this.mapPairs.find(mp => mp.val == val);
        if (mp == null) return null;
        return mp.text;
    }
    getValueColor(val: any): string {
        let mp = this.mapPairs.find(mp => mp.val == val);
        if (mp == null) return null;
        return mp.colorHex;
    }
}

export class CtrlrTemplParam {
    constructor(name: string, desc: string, dType: DType, canUseOPTag: boolean, canEdit: boolean, kPIGroup: KPIGroup, ctrlrTemplTabId: CtrlrVarType, canEditTempl: boolean, cssClass: string,
        viewHeader: string, viewHeaderGr: string, mapping: Mapping, numberFormat: NumberFormat) {
        this.name = name;
        this.desc = desc;
        this.dType = dType;
        this.canUseOPTag = canUseOPTag;
        this.canEdit = canEdit;
        this.kPIGroup = kPIGroup;
        this.ctrlrTemplTabId = ctrlrTemplTabId;
        this.canEditTempl = canEditTempl;
        this.cssClass = cssClass;
        this.viewHeader = viewHeader;
        this.viewHeaderGr = viewHeaderGr;
        this.mapping = mapping;
        this.numberFormat = numberFormat;
    }
    name: string;
    desc: string;
    dType: DType;
    canUseOPTag: boolean;
    canEdit: boolean;
    kPIGroup: KPIGroup;
    ctrlrTemplTabId: CtrlrVarType;
    canEditTempl: boolean
    cssClass: string;
    viewHeader: string;
    viewHeaderGr: string;
    mapping: Mapping;
    numberFormat: NumberFormat;
}

//This is used during Controller template configuration
export class CtrlrTemplVw {
    constructor(id: string, name: string, ctrlrTemplTabVws: CtrlrTemplTabVw[], mappings: Mapping[]) {
        if (id == null) this.id = Global.newGuid(); else this.id = id;
        this.name = name;
        this.tabs = ctrlrTemplTabVws;
        this.mappings = mappings;
    }
    id: string;
    name: string;
    tabs: CtrlrTemplTabVw[];
    mappings: Mapping[];

    toCtrlrTempl(): CtrlrTempl {
        let params: CtrlrTemplParam[] = [];
        this.tabs.forEach(t => params = params.concat(t.params));
        let ret = new CtrlrTempl(this.id, this.name, params, this.mappings)
        return ret;
    }
}

export class CtrlrTemplTabVw {
    constructor(name: string, showKPIGroup: boolean, ctrlrTempl: CtrlrTempl, params: CtrlrTemplParam[], ctrlrTemplTabId: CtrlrVarType) {
        this.name = name;
        this.showKPIGroup = showKPIGroup;
        this.params = params;
        this.ctrlrTempl = ctrlrTempl;
        this.ctrlrTemplTabId = ctrlrTemplTabId;
    }
    name: string;
    showKPIGroup: boolean;
    params: CtrlrTemplParam[];
    ctrlrTempl: CtrlrTempl;
    ctrlrTemplTabId: CtrlrVarType;
}
