  public static generateNewCtrlrConfRow(num: number, tagName: string, templParams: CtrlrTemplParam[]): CtrlrConfRow {
    //console.log(templParams);
    let ctrlrConfRowCols: CtrlrConfRowCol[] = [];
    templParams.forEach(pTempl => {
      switch (pTempl.name) {
        case 'Number': ctrlrConfRowCols = ctrlrConfRowCols.concat(new CtrlrConfRowCol(num, pTempl.canUseOPTag, pTempl)); break;
        case 'Description': ctrlrConfRowCols = ctrlrConfRowCols.concat(new CtrlrConfRowCol(tagName + " Description", pTempl.canUseOPTag, pTempl)); break;
        default: {
          ctrlrConfRowCols = ctrlrConfRowCols.concat(new CtrlrConfRowCol(tagName + "." + pTempl.name, true, pTempl)); break;
        }
      }
    });
    return new CtrlrConfRow(null, ctrlrConfRowCols);
  }

