import { DemoData } from './../demo';
import { timer } from 'rxjs';
import { OPFetch, FetchItem } from './../global';
import { DataService } from './../data.service';
import { CtrlrConf, CtrlrVw, CtrlrMatrixVw } from './../model/model-ctrlr-conf';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { MsgBoxObj, CentreMsgObject, Selected } from 'open-plant-ng';
import { CtrlrTopStripVw, DataCell } from '../model/model-ctrlr';
import { CtrlrVarType } from '../model/enums';


@Component({
  selector: 'app-ctrlr',
  templateUrl: './ctrlr.component.html',
  styleUrls: ['./ctrlr.component.css']
})
export class CtrlrComponent implements OnInit {

  constructor(private route: ActivatedRoute, private router: Router, private cdr: ChangeDetectorRef, private dataService: DataService) { }
  ctrlrTagName: string;
  msgBox: MsgBoxObj;
  centreMsg: CentreMsgObject;
  ctrlrConf: CtrlrConf;
  oPFetch: OPFetch;
  ctrlrTopStripVw: CtrlrTopStripVw;
  ctrlrVw: CtrlrVw;
  ctrlrMatrixVw: CtrlrMatrixVw;

  ngAfterViewInit() {
    this.cdr.detectChanges();
  }
  ngOnInit() {
    if (!DemoData.initialized) DemoData.initialize();
    this.route.params.subscribe((params) => {
      this.ctrlrTagName = params.ctrlrTagName;
      console.log(this.ctrlrTagName);
    });
  }

  onCMReady(cMObj) {
    this.centreMsg = cMObj;
    this.centreMsg.showProcess('Loading...');

    //DEMO DATA GENERATE
    this.ctrlrConf = this.dataService.getCtrlrConf(this.ctrlrTagName);


    //Create The Views    
    this.ctrlrTopStripVw = this.ctrlrConf.getCtrlrTopStripVw(); //topstrip view
    this.ctrlrVw = new CtrlrVw(); //controller details view
    this.ctrlrVw.ctrlrTagName = this.ctrlrConf.ctrlrTagName;
    this.ctrlrVw.description = this.ctrlrConf.description
    this.ctrlrVw.numCV = this.ctrlrConf.ctrlrConfTabs.find(t => t.tabId == CtrlrVarType.CV).rows.length;
    this.ctrlrVw.numMV = this.ctrlrConf.ctrlrConfTabs.find(t => t.tabId == CtrlrVarType.MV).rows.length;
    this.ctrlrVw.numDV = this.ctrlrConf.ctrlrConfTabs.find(t => t.tabId == CtrlrVarType.DV).rows.length;
    this.ctrlrVw.dataCells = [];
    this.ctrlrConf.mainParams.forEach(mp => {
      if (mp.ctrlrTemplParam.name == "Mode" || mp.ctrlrTemplParam.name == "Status" || mp.ctrlrTemplParam.name == "Benefit") return;
      if (mp.isOPTag) this.ctrlrVw.dataCells = this.ctrlrVw.dataCells.concat(new DataCell(null, mp.value, null, mp.ctrlrTemplParam));
      else this.ctrlrVw.dataCells = this.ctrlrVw.dataCells.concat(new DataCell(mp.value, null, null, mp.ctrlrTemplParam));
    });
    this.ctrlrMatrixVw = this.ctrlrConf.getCtrlrMatrixVw(); //controller matrix


    //Create OP Fetch
    this.oPFetch = new OPFetch();
    this.ctrlrTopStripVw.fetchItems.forEach(fi => this.oPFetch.add(fi.tagId, fi.propName, fi.fetchOnce));
    this.ctrlrVw.dataCells.forEach(dc => { if (dc.tagId != null) this.oPFetch.add(dc.tagId, dc.ctrlrTemplParam.name, false); });


    //REFRESH THE DEMO DATA
    const source = timer(0, 2000);
    source.subscribe(val => {
      this.oPFetch.refresh();

      //Update the Top Strip
      let ctrlrStatusN = this.oPFetch.getResultByProp("Status");
      if (this.ctrlrTopStripVw.statusMapping != null) {
        this.ctrlrTopStripVw.status = this.ctrlrTopStripVw.statusMapping.getValueText(ctrlrStatusN);
        this.ctrlrTopStripVw.statusColor = this.ctrlrTopStripVw.statusMapping.getValueColor(ctrlrStatusN);
      } else this.ctrlrTopStripVw.status = ctrlrStatusN.toString();
      let ctrlrModeN = this.oPFetch.getResultByProp("Mode");
      if (this.ctrlrTopStripVw.modeMapping != null) {
        this.ctrlrTopStripVw.mode = this.ctrlrTopStripVw.modeMapping.getValueText(ctrlrModeN);
        this.ctrlrTopStripVw.modeColor = this.ctrlrTopStripVw.modeMapping.getValueColor(ctrlrModeN);
      } else this.ctrlrTopStripVw.mode = ctrlrModeN.toString();
      this.ctrlrTopStripVw.benefitN = this.oPFetch.getResultByProp("Benefit");
      this.ctrlrTopStripVw.benefitColor = "#28a745";

      //Update the View
      this.ctrlrVw.dataCells.forEach(dc => {
        if (dc.tagId != null) dc.setValue(this.oPFetch.getResultStr(dc.tagId, dc.ctrlrTemplParam.mapping));
      });
    });
    this.centreMsg.stop();
  }


  onMBReady(mBObj) {
    this.msgBox = mBObj;
  }

}
