import { DType, KPIGroup, NumberFormat } from './../../model/enums';
import { EnumKVPRenderer, EnumKVP } from './../../services/enum-kvp-renderer.component';
import { EnumRenderer } from '../../services/enum-renderer.component';
import { MsgBoxObj, CentreMsgObject, Selected } from 'open-plant-ng';
import { CtrlrTemplTabVw, CtrlrTemplParam } from '../../model/model-ctrlr-templ';
import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { ButtonRendererComponent } from '../../services/button-renderer.component';
import { GridOptions } from 'ag-grid-community';
@Component({
  selector: 'app-tabgrid2',
  templateUrl: './tabgrid2.component.html',
  styleUrls: ['./tabgrid2.component.css']
})
export class Tabgrid2Component implements OnInit {
  @Input() ctrlrTemplTabVw: CtrlrTemplTabVw;
  @Output() ctrlrTemplTabVwChange: EventEmitter<CtrlrTemplTabVw> = new EventEmitter<CtrlrTemplTabVw>();
  @Input() centreMsg: CentreMsgObject;
  @Input() msgBox: MsgBoxObj;
  columnDefs = [];
  pTemplBef: CtrlrTemplParam[] = [];
  gridApi;
  constructor() {

  }

  onGridReady(params) {
    this.gridApi = params.api;
  }


  ngOnInit() {
    for (let obj of this.ctrlrTemplTabVw.params) {
      this.pTemplBef = this.pTemplBef.concat(obj);
      obj.viewHeader
    }
    let mappingKVPs: EnumKVP[] = [];
    mappingKVPs = mappingKVPs.concat(new EnumKVP(null, "No Mapping"));
    this.ctrlrTemplTabVw.ctrlrTempl.mappings.forEach(m => mappingKVPs = mappingKVPs.concat(new EnumKVP(m.id, m.name)));

    this.columnDefs = [
      {
        headerName: 'Parameter', field: 'name', resizable: true, editable: function (e) { return e.data.canEditTempl; }, cellStyle: { 'display': 'flex', 'align-items': 'center', }, flex: 1
      },
      {
        headerName: 'Description', field: 'desc', editable: function (e) { return e.data.canEditTempl; }, cellStyle: { 'display': 'flex', 'align-items': 'center', }, flex: 1
      },
      {
        headerName: 'Data Type', cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' },
        headerClass: "op-header-center", field: "dType", width: 100, cellRendererFramework: EnumRenderer,
        cellRendererParams: {
          enumVals: ["Number", "Text", "Boolean"],
          isVisible: function (e) { return true },
          disabled: function (e) { return !e.canEditTempl },
          //selectClass: 'custom-select-sm'
          //selectStyle: { 'text-align-last': 'center' }
        }
      },
      {
        headerName: 'Number Format', cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' },
        headerClass: "op-header-center", field: "numberFormat", width: 130, cellRendererFramework: EnumRenderer,
        cellRendererParams: {
          enumVals: ["None", "TwoDecimals", "Percentage"],
          isVisible: function (e: CtrlrTemplParam) { return true },
          disabled: function (e: CtrlrTemplParam) { return !e.canEditTempl },
          //selectClass: 'custom-select-sm'
          //selectStyle: { 'text-align-last': 'center' }
        }
      },
      {
        headerName: 'Mapping', cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' },
        headerClass: "op-header-center", field: "mapping", width: 120, cellRendererFramework: EnumKVPRenderer,
        cellRendererParams: {
          enumKVPs: mappingKVPs,
          isVisible: function (e) { return true },
          disabled: function (e) { return !e.canEditTempl },
          //selectClass: 'custom-select-sm'
          //selectStyle: { 'text-align-last': 'center' }
        }
      },
      {
        headerName: 'KPI Group', hide: !this.ctrlrTemplTabVw.showKPIGroup, cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' }, headerClass: "op-header-center", field: "kPIGroup", width: 100, cellRendererFramework: EnumRenderer,
        cellRendererParams: {
          enumVals: ["Performance", "Optimization", "Control", "Process"],
          isVisible: function (e) { return e.canEditTempl }
        }
      },
      {
        headerName: 'View Header Name', field: 'viewHeader', editable: true, cellStyle: { 'display': 'flex', 'align-items': 'center', }, flex: 1
      },
      {
        headerName: 'View Header Group', field: 'viewHeaderGr', editable: true, cellStyle: { 'display': 'flex', 'align-items': 'center', }, flex: 1
      },
      {
        headerName: 'Delete', cellRendererFramework: ButtonRendererComponent, width: 60, cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' }, headerClass: "op-header-center",
        cellRendererParams: {
          onClick: this.deleteBtnClick.bind(this),
          label: '',
          isVisible: function (e) { return e.canEditTempl },
          iconClass: 'fa fa-trash',
          btnClass: 'btn btn-outline-primary border-0 py-0 px-1 '
        }
      }
    ];
  }



  deleteBtnClick(e) {

    let ctrlrTemplParam: CtrlrTemplParam = e.rowData;
    this.ctrlrTemplTabVw.params = this.ctrlrTemplTabVw.params.filter(i => i.name != ctrlrTemplParam.name);
  }

  undoClick() {
    this.ctrlrTemplTabVw.params = this.ctrlrTemplTabVw.params.slice(0, 0);
    for (let obj of this.pTemplBef) {
      this.ctrlrTemplTabVw.params = this.ctrlrTemplTabVw.params.concat(obj)
    }
  }
  gridOptions: GridOptions = {
    suppressScrollOnNewData: true,
  }
  handleRowDataChanged(event) {
    const index = this.ctrlrTemplTabVw.params.length - 1;
    this.gridOptions.api.ensureIndexVisible(index, 'bottom');
  }
  newClick() {
    this.ctrlrTemplTabVw.params = this.ctrlrTemplTabVw.params.concat(new CtrlrTemplParam("NewParameter" + (this.ctrlrTemplTabVw.params.length + 1), "New Parameter"
      + (this.ctrlrTemplTabVw.params.length + 1), DType.number, true, true, KPIGroup.NA, this.ctrlrTemplTabVw.ctrlrTemplTabId, true, "", null, null, null, NumberFormat.TwoDecimals));

    //this.gridApi.setFocusedCell(this.ctrlrTemplTabVw.params.length, 0, null);
  }
}
