import { Utility } from './../utility';
import { DemoData } from './../demo';
import { ButtonRendererComponent } from '../services/button-renderer.component';
import { DataService } from '../data.service';
import { MsgBoxObj, CentreMsgObject, Selected } from 'open-plant-ng';
import { Component, OnInit } from '@angular/core';
import { CtrlrTempl, CtrlrTemplVw, CtrlrTemplParam } from './../model/model-ctrlr-templ';
@Component({
  selector: 'app-conf-ctrlr-templ',
  templateUrl: './conf-ctrlr-templ.component.html',
  styleUrls: ['./conf-ctrlr-templ.component.css']
})


export class ConfCtrlrTemplComponent implements OnInit {

  constructor(private dataService: DataService) { }
  ctrlrTempls: CtrlrTempl[] = [];
  selCtrlrTempl: CtrlrTempl = null;
  editOrNew: string = "Edit";
  columnDefs = [
    {
      headerName: 'Controller Template', field: 'name', sortable: true, cellStyle: { 'display': 'flex', 'align-items': 'center' }, flex: 1,
    },
    {
      headerName: 'Edit', cellRendererFramework: ButtonRendererComponent, width: 60, cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        onClick: this.editBtnClick.bind(this),
        label: '',
        iconClass: 'fa fa-edit',
        btnClass: 'btn btn-outline-primary border-0 py-0 px-1 '
      }
    },
    {
      headerName: 'Delete', cellRendererFramework: ButtonRendererComponent, width: 60, cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        onClick: this.deleteBtnClick.bind(this),
        label: '',
        iconClass: 'fa fa-trash',
        btnClass: 'btn btn-outline-primary border-0 py-0 px-1 '
      }
    }
  ];

  ngOnInit() {
    if (!DemoData.initialized) DemoData.initialize();
  }

  onGridReady(params) {
    this.centreMsg.showProcess('Loading...');
    this.ctrlrTempls = this.dataService.getCtrlrTempls();
    this.centreMsg.stop();
  }

  editBtnClick(e) {
    this.selCtrlrTempl = e.rowData;
    this.editOrNew = "Edit";
    //this.showEdit = true;

  }

  deleteBtnClick(e) {
    let ctrlrTempl: CtrlrTempl = e.rowData;
    this.msgBox.showYesNo('Delete \'' + ctrlrTempl.name + '\' Template?', (selected: Selected) => {
      if (selected == Selected.Yes) {
        this.centreMsg.showProcess('Deleting \'' + ctrlrTempl.name + '\' Template...');
        setTimeout(() => {
          this.dataService.deleteCtrlrTempl(ctrlrTempl.id);
          this.ctrlrTempls = this.ctrlrTempls.filter(i => i.id !== ctrlrTempl.id);
          this.centreMsg.stop();
        }, 500);
      }
    })
  }

  newTemplClick() {
    this.editOrNew = "New";
    this.selCtrlrTempl = Utility.createBasicCtrlrTempl();
    //this.showEdit = true;
  }

  backClick() {
    this.selCtrlrTempl = null;
    this.centreMsg.showProcess('Loading...');
    this.ctrlrTempls = this.dataService.getCtrlrTempls();
    this.centreMsg.stop();
  }
  centreMsg: CentreMsgObject;
  onCMReady(param) {
    this.centreMsg = param;
  }

  msgBox: MsgBoxObj;
  onMBReady(param) {
    this.msgBox = param;
  }
}
