import { CtrlrMatrixVw, CtrlrMatrixRowVw, CtrlrMatrixCellVw } from './../model/model-ctrlr-conf';
import { Component, OnInit, Input, AfterViewInit, ChangeDetectorRef, EventEmitter, Output, ViewChild, ElementRef } from '@angular/core';
import { CtrlrMatrixCellType } from '../model/enums';

@Component({
  selector: 'app-ctrlr-matrix',
  templateUrl: './ctrlr-matrix.component.html',
  styleUrls: ['./ctrlr-matrix.component.css']
})
export class CtrlrMatrixComponent implements OnInit, AfterViewInit {

  constructor(private cdr: ChangeDetectorRef) { }

  @Input() ctrlrMatrixVw: CtrlrMatrixVw;
  @Output() ctrlrMatrixVwChange: EventEmitter<CtrlrMatrixVw> = new EventEmitter<CtrlrMatrixVw>();
  CtrlrMatrixCellType = CtrlrMatrixCellType;
  rowSel: number;
  colSel: number;

  @ViewChild('cellMatrix', { static: false }) cellMatrix: ElementRef;

  @Input() editable: boolean;

  ngOnInit() {

  }
  ngAfterViewInit() {
    this.cdr.detectChanges();
  }

  cellClick(cell: CtrlrMatrixCellVw, row: CtrlrMatrixRowVw) {
    if (this.editable == false) return;
    this.ctrlrMatrixVw.rows.forEach(r => r.cells.forEach(c => c.editMode = false));
    cell.editMode = true;
    cell.prevValue = cell.value;
    setTimeout(() => { // this will make the execution after the above boolean has changed
      if (this.cellMatrix != null) this.cellMatrix.nativeElement.focus();
    }, 0)
  }

  enterKeyPress(cell: CtrlrMatrixCellVw) {
    cell.editMode = false;
  }

  escapeKeyPress(cell: CtrlrMatrixCellVw) {
    cell.value = cell.prevValue;
    cell.editMode = false;
  }

  onFocusOut(cell: CtrlrMatrixCellVw) {
    cell.editMode = false;
  }

  cellMouseEnter(r, c) {
    this.rowSel = r;
    this.colSel = c;
  }

  cellMouseLeave() {
    this.rowSel = -1;
    this.colSel = -1;

  }
}
