export class CtrlrConfRow {
    constructor(id: string, cols: CtrlrConfRowCol[]) {
        this.cols = cols;
        if (id == null) this.id = Global.newGuid(); else this.id = id;
    }
    id: string;
    cols: CtrlrConfRowCol[];
    getBasicInfo(): XVBasicInfoVw {
        let num = this.cols.find(c => c.ctrlrTemplParam.name == "Number").value;
        let desc = this.cols.find(c => c.ctrlrTemplParam.name == "Description").value;
        return new XVBasicInfoVw(num, desc);
    }
}


export class CtrlrConfRowCol {
    constructor(value: any, isOPTag: boolean, ctrlrTemplParam: CtrlrTemplParam) {
        this.value = value;
        this.isOPTag = isOPTag;
        this.ctrlrTemplParam = ctrlrTemplParam;
    }
    value: any; //Can be a number or string or null... therefore use ANY
    isOPTag: boolean;
    ctrlrTemplParam: CtrlrTemplParam;
}


