import { Utility } from './utility';
import { DemoData } from './demo';
import { CtrlrConfRowCol, CtrlrConf, CtrlrConfVw, CtrlrConfTab, CtrlrConfRow, CtrlrConfListVw } from './model/model-ctrlr-conf';
import { DType, KPIGroup, CtrlrVarType, NumberFormat } from './model/enums'
import { Global } from './global';
import { CtrlrHomeVw } from './model/model-ctrlr';
import { Injectable } from '@angular/core';
import { CtrlrTempl, CtrlrTemplVw, CtrlrTemplParam, Mapping, MapPair } from './model/model-ctrlr-templ';
@Injectable({
  providedIn: 'root'
})
export class DataService {
  private baseLocation: string;
  enableDemo: boolean;
  constructor() {
    this.enableDemo = true;
    this.baseLocation = window.location.host;
  }

  //******************************
  //Controller Templates
  //******************************
  getCtrlrTempls(): CtrlrTempl[] {
    let Templates: CtrlrTempl[];
    //if (this.enableDemo) Templates = DemoData.getCtrlrTempls();
    if (this.enableDemo) Templates = DemoData.getCtrlrTempls();
    //Global.ctrlrTempls = Templates;
    return Templates;
  }


  deleteCtrlrTempl(id: string) {
    if (this.enableDemo) {
      DemoData.ctrlrTempls = DemoData.ctrlrTempls.filter(t => t.id != id);
      //Global.ctrlrTempls = Global.ctrlrTempls.filter(t => t.id != id);
    }
    return true;
  }

  saveCtrlrTempl(ctrlrTempl: CtrlrTempl) {
    if (this.enableDemo) {
      let i = DemoData.ctrlrTempls.findIndex(t => t.id == ctrlrTempl.id);
      if (i >= 0) {
        console.log("Saving Controller Template '" + ctrlrTempl.name + "'");
        DemoData.ctrlrTempls[i] = ctrlrTempl;
        //Global.ctrlrTempls[i] = ctrlrTempl;
        return true;
      } else {
        console.log("Inserting new Controller Template '" + ctrlrTempl.name + "'");
        DemoData.ctrlrTempls = DemoData.ctrlrTempls.concat(ctrlrTempl);
        //Global.ctrlrTempls = Global.ctrlrTempls.concat(ctrlrTempl);
        return true;
      }
    }

    return true;
  }


  //******************************
  //Controllers
  //******************************

  getCtrlrConfListVw(): CtrlrConfListVw[] {
    if (this.enableDemo) return DemoData.getCtrlrConfListVw();
    return [];
  }

  getCtrlrHomeVws(): CtrlrHomeVw[] {
    if (this.enableDemo) return DemoData.getCtrlrHomeVws();
    return [];
  }


  getCtrlrConfs(): CtrlrConf[] {
    let ctrlrConfs: CtrlrConf[] = [];
    if (this.enableDemo) ctrlrConfs = DemoData.getCtrlrConfs();
    return ctrlrConfs;
  }

  getCtrlrConf(ctrlrTagName: string): CtrlrConf {
    let ctrlrConf: CtrlrConf = null;
    if (this.enableDemo) ctrlrConf = DemoData.getCtrlrConf(ctrlrTagName);
    return ctrlrConf;
  }

  // createCtrlrConf(tagName: string, ctrlrTempl: CtrlrTempl, ctrlrId: string = null) {
  //   if (this.enableDemo) 

  //   if (Global.ctrlrTempls == null) this.getCtrlrTempls();
  //   let newCtrlr: CtrlrConf = this.generateDemoCtrlrConf(ctrlrTempl);
  //   return newCtrlr;
  // }

  deleteCtrlrConf(ctrlrIds: string[]): boolean {
    if (this.enableDemo) {
      ctrlrIds.forEach(id => {
        DemoData.ctrlrConfs = DemoData.ctrlrConfs.filter(c => c.id != id);
      })
    }
    return true
  }

  saveCtrlrConf(ctrlrConf: CtrlrConf) {
    if (this.enableDemo) {
      let i = DemoData.ctrlrConfs.findIndex(c => c.id == ctrlrConf.id);
      if (i >= 0) {
        DemoData.ctrlrConfs[i] = ctrlrConf;
      } else {
        DemoData.ctrlrConfs = DemoData.ctrlrConfs.concat(ctrlrConf);
      }

    }
    return true;
  }
}


