import { DemoData } from './../demo';
import { CtrlrConfRowVw, CtrlrConfRow, CtrlrConfTab } from '../model/model-ctrlr-conf';
import { CheckBoxRendererComponent } from './../services/checkbox-renderer.component';
import { MsgBoxObj, CentreMsgObject } from 'open-plant-ng';
import { ButtonRendererComponent } from './../services/button-renderer.component';
import { CtrlrConfVw, CtrlrConf } from '../model/model-ctrlr-conf';
import { CtrlrTempl, CtrlrTemplParam } from '../model/model-ctrlr-templ';
import { DataService } from './../data.service';
import { Component, OnInit, EventEmitter } from '@angular/core';
import { discardPeriodicTasks } from '@angular/core/testing';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-conf-ctrlr',
  templateUrl: './conf-ctrlr-edit.component.html',
  styleUrls: ['./conf-ctrlr-edit.component.css']
})
export class ConfCtrlrEditComponent implements OnInit {

  constructor(private route: ActivatedRoute, private dataService: DataService) { }
  selCtrlrConf: CtrlrConfVw;
  ctrlrTempls: CtrlrTempl[];
  delCount: number = 0;


  ngOnInit() {
    if (!DemoData.initialized) DemoData.initialize();
    this.route.params.subscribe((params) => {
      let ctrlrTagName = params.ctrlrTagName;
      let ctrlrConf = this.dataService.getCtrlrConf(ctrlrTagName);
      this.selCtrlrConf = new CtrlrConfVw(ctrlrConf);
    });
  }

  onGridReady(params) { }


  //SAVE ****************************
  private saveInitiated: EventEmitter<boolean> = new EventEmitter(); // [saveInitiated]="saveInitiated" is set on every tab

  private tabUpdated: number;
  onSaveCtrlrConf() {
    console.log('Saving...')
    this.tabUpdated = 0;
    this.saveInitiated.emit(true); //Tell children to export the latest CtrlrConfTab model
  }

  onModelUpdated(updatedCtrlrConfTab: CtrlrConfTab) { //(modelUpdated)="onModelUpdated($event)" is set. CtrlrConfTab Model will call this function after this.saveInitiated.emit(true) is done
    //console.log(updatedCtrlrConfTab);
    let tempTab = this.selCtrlrConf.ctrlrConfTabs.find(t => t.tabId == updatedCtrlrConfTab.tabId);
    tempTab.rows = updatedCtrlrConfTab.rows;
    this.tabUpdated++;
    if (this.tabUpdated == 4) {
      this.centreMsg.showProcess('Saving...');
      setTimeout(() => {
        let ctrlrConfToSave = this.selCtrlrConf.toCtrlrConf();
        this.dataService.saveCtrlrConf(ctrlrConfToSave);
        this.centreMsg.showSuccess('Save success!', null, 500);
        console.log('Save Completed!')
      }, 250);

    }
  }




  centreMsg: CentreMsgObject;
  onCMReady(param) {
    this.centreMsg = param;
  }

  msgBox: MsgBoxObj;
  onMBReady(param) {
    this.msgBox = param;
  }



}
