import { CharttestComponent } from './charttest/charttest.component';
import { ConfCtrlrEditNewComponent } from './conf-ctrlr-edit-new/conf-ctrlr-edit-new.component';
import { ConfCtrlrListComponent } from './conf-ctrlr-list/conf-ctrlr-list.component';
import { LoginDemoComponent } from './login-demo/login-demo.component';
import { CtrlrXvComponent } from './ctrlr-xv/ctrlr-xv.component';
import { ConfCtrlrTemplComponent } from './conf-ctrlr-templ/conf-ctrlr-templ.component';
import { HomeComponent } from './home/home.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CtrlrComponent } from './ctrlr/ctrlr.component';
import { ConfCtrlrEditComponent } from './conf-ctrlr-edit/conf-ctrlr-edit.component';

const routes: Routes = [
  { path: '', component: LoginDemoComponent },
  { path: 'login', component: LoginDemoComponent },
  { path: 'home', component: HomeComponent },
  { path: 'controllers/', component: HomeComponent },
  { path: 'new-controller', component: ConfCtrlrEditNewComponent },
  { path: 'configure-controller', component: ConfCtrlrListComponent },
  { path: 'configure-controller/:ctrlrTagName', component: ConfCtrlrEditComponent },
  { path: 'configure-controller-template', component: ConfCtrlrTemplComponent },
  { path: 'controllers/:ctrlrTagName', component: CtrlrComponent },
  { path: 'controllers/:ctrlrTagName/:ctrlrVarTypeStr', component: CtrlrXvComponent },
  { path: 'test', component: CharttestComponent },

];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
