import { Mapping } from './../model/model-ctrlr-templ';
import { timer } from 'rxjs';
import { Global, OPFetch } from '../global';
import { CtrlrConf } from '../model/model-ctrlr-conf';
import { DataService } from '../data.service';
import { Component, OnInit, ViewChild, AfterViewInit, ChangeDetectorRef, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MsgBoxObj, CentreMsgObject, Selected } from 'open-plant-ng';
import { KPIGroup, CtrlrVarType } from '../model/enums';
import { CtrlrXVKPIVw, HeaderCell, DataCell, DataRow, CtrlrTopStripVw } from '../model/model-ctrlr';
import { Router } from '@angular/router';




@Component({
  selector: 'app-ctrlr-xv',
  templateUrl: './ctrlr-xv.component.html',
  styleUrls: ['./ctrlr-xv.component.css']
})
export class CtrlrXvComponent implements OnInit, AfterViewInit {

  constructor(private route: ActivatedRoute, private dataService: DataService, private router: Router, private cdr: ChangeDetectorRef) { }



  ctrlrTagName: string;
  msgBox: MsgBoxObj;
  centreMsg: CentreMsgObject;
  ctrlrConf: CtrlrConf;
  oPFetch: OPFetch;
  ctrlrXVKPIVws: CtrlrXVKPIVw[];
  ctrlrVarType: CtrlrVarType;

  ctrlrTopStripVw: CtrlrTopStripVw

  ngOnInit() {
    this.route.params.subscribe((params) => {
      this.ctrlrTagName = params.ctrlrTagName;
      if (params.ctrlrVarTypeStr == "cv") this.ctrlrVarType = CtrlrVarType.CV;
      if (params.ctrlrVarTypeStr == "mv") this.ctrlrVarType = CtrlrVarType.MV;
      if (params.ctrlrVarTypeStr == "dv") this.ctrlrVarType = CtrlrVarType.DV;
    });
  }
  ngAfterViewInit() {

    this.cdr.detectChanges();
  }

  onCMReady(cMObj) {
    this.centreMsg = cMObj;
    this.centreMsg.showProcess('Loading...');

    //DEMO DATA GENERATE
    //await Global.delay(500);
    this.ctrlrConf = this.dataService.getCtrlrConf(this.ctrlrTagName);


    //TRANSFORM CtrlrConf INTO VIEW MODEL CtrlrXVVw WHILE BUILDING THE DATA FETCH (OPFETCH)
    let prevHeaderCell: HeaderCell = null;
    this.ctrlrXVKPIVws = [];
    this.oPFetch = new OPFetch();


    //BUILD TOP STRIP FOR MODE, STATUS AND BENEFIT
    this.ctrlrTopStripVw = this.ctrlrConf.getCtrlrTopStripVw();
    this.ctrlrTopStripVw.fetchItems.forEach(fi => this.oPFetch.add(fi.tagId, fi.propName, fi.fetchOnce));


    //BUILD ctrlrXVVws - Go through KPIGroup (Summary, Performance, Optimization, Tuning, Process)
    for (let item in KPIGroup) {
      if (!isNaN(Number(item)) || item == "NA") continue; //Ignore KPIGroup.NA     

      //check how many properties are in the KPI Group, if there is none, return.   
      let nProp: number = 0;
      this.ctrlrConf.ctrlrTempl.params.forEach(p => {
        if (p.ctrlrTemplTabId != this.ctrlrVarType) return;
        if (p.kPIGroup != Number(KPIGroup[item])) return;
        if (p.name == "Number" || p.name == "Description") return
        nProp++;
      });
      if (nProp == 0) continue;


      let fetchOnce: boolean = (item != "Summary");
      let headerTopRow: HeaderCell[] = [];
      let headerBotRow: string[] = [];
      let numberTempl = this.ctrlrConf.ctrlrTempl.params.find(p => p.name == "Number");
      headerTopRow = headerTopRow.concat(new HeaderCell(2, 1, numberTempl.viewHeader));
      let descTempl = this.ctrlrConf.ctrlrTempl.params.find(p => p.name == "Description");
      headerTopRow = headerTopRow.concat(new HeaderCell(2, 1, descTempl.viewHeader));

      this.ctrlrConf.ctrlrTempl.params.forEach(p => { //Traverse through Params

        if (p.kPIGroup == Number(KPIGroup[item]) && p.ctrlrTemplTabId == this.ctrlrVarType) { //Find params that matches an xVType and KPIGroup
          if (p.name == "Number" || p.name == "Description") return; //don't process Number and Description cause it is added
          //BUILD HEADER
          if (p.viewHeaderGr == null) {
            headerTopRow = headerTopRow.concat(new HeaderCell(2, 1, p.viewHeader));

          } else {
            if (prevHeaderCell == null || prevHeaderCell.header != p.viewHeaderGr) {
              prevHeaderCell = new HeaderCell(1, 1, p.viewHeaderGr);
              headerTopRow = headerTopRow.concat(prevHeaderCell);
            } else prevHeaderCell.colSpan++;
            headerBotRow = headerBotRow.concat(p.viewHeader.replace(" ", "\n"));
          }
        }
      });

      let tabLabel = CtrlrVarType[this.ctrlrVarType] + " " + item;
      let name = CtrlrVarType[this.ctrlrVarType] + item;


      //BUILD DATA ROWS AND OP FETCH   
      let dataRows: DataRow[] = [];
      let ctrlrConfTab = this.ctrlrConf.ctrlrConfTabs.find((t => t.tabId == this.ctrlrVarType));
      ctrlrConfTab.rows.forEach((r, ri) => {
        let dataCells: DataCell[] = [];
        let numberParam = r.cols.find(c => c.ctrlrTemplParam.name == "Number");
        dataCells = dataCells.concat(new DataCell(numberParam.value, null, null, numberParam.ctrlrTemplParam));
        let descParam = r.cols.find(c => c.ctrlrTemplParam.name == "Description");
        dataCells = dataCells.concat(new DataCell(descParam.value, null, null, descParam.ctrlrTemplParam));

        r.cols.forEach((c, ci) => {
          if (c.ctrlrTemplParam.kPIGroup == Number(KPIGroup[item])) {
            if (c.ctrlrTemplParam.name == "Number" || c.ctrlrTemplParam.name == "Description") return;
            if (c.isOPTag) {
              this.oPFetch.add(c.value, c.ctrlrTemplParam.name, fetchOnce);
              dataCells = dataCells.concat(new DataCell(null, c.value, null, c.ctrlrTemplParam));
            } else dataCells = dataCells.concat(new DataCell(null, c.value, null, c.ctrlrTemplParam));
          }
        });
        dataRows = dataRows.concat(new DataRow(dataCells));
      });
      this.ctrlrXVKPIVws = this.ctrlrXVKPIVws.concat(new CtrlrXVKPIVw(name, tabLabel, this.ctrlrVarType, headerTopRow, headerBotRow, dataRows));
    }


    //REFRESH THE DEMO DATA
    const source = timer(0, 2000);
    source.subscribe(val => {
      this.oPFetch.refresh();

      //Update the Top Strip
      let ctrlrStatusN = this.oPFetch.getResultByProp("Status");
      if (this.ctrlrTopStripVw.statusMapping != null) {
        this.ctrlrTopStripVw.status = this.ctrlrTopStripVw.statusMapping.getValueText(ctrlrStatusN);
        this.ctrlrTopStripVw.statusColor = this.ctrlrTopStripVw.statusMapping.getValueColor(ctrlrStatusN);
      } else this.ctrlrTopStripVw.status = ctrlrStatusN.toString();
      let ctrlrModeN = this.oPFetch.getResultByProp("Mode");
      if (this.ctrlrTopStripVw.modeMapping != null) {
        this.ctrlrTopStripVw.mode = this.ctrlrTopStripVw.modeMapping.getValueText(ctrlrModeN);
        this.ctrlrTopStripVw.modeColor = this.ctrlrTopStripVw.modeMapping.getValueColor(ctrlrModeN);
      } else this.ctrlrTopStripVw.mode = ctrlrModeN.toString();
      this.ctrlrTopStripVw.benefitN = this.oPFetch.getResultByProp("Benefit");
      this.ctrlrTopStripVw.benefitColor = "#28a745";

      //Update the Tab Tables
      this.ctrlrXVKPIVws.forEach(ctrlrXVKPIVw => {
        let Temp: CtrlrXVKPIVw = ctrlrXVKPIVw;
        Temp.dataRows.forEach(dataRow => {
          dataRow.dataCells.forEach(dataCell => {
            if (dataCell.tagId != null) dataCell.setValue(this.oPFetch.getResult(dataCell.tagId));
          })
        })
      })
    });
    this.centreMsg.stop();
  }


  onMBReady(mBObj) {
    this.msgBox = mBObj;
  }



}

function delay(ms: number) {
  return new Promise(resolve => setTimeout(resolve, ms));
}
