import { CtrlrConfListVw } from './../model/model-ctrlr-conf';
import { DemoData } from './../demo';
import { Router } from '@angular/router';
import { CheckBoxRendererComponent } from './../services/checkbox-renderer.component';
import { MsgBoxObj, CentreMsgObject, Selected } from 'open-plant-ng';
import { ButtonRendererComponent } from './../services/button-renderer.component';
import { CtrlrConfVw, CtrlrConf } from '../model/model-ctrlr-conf';
import { CtrlrTempl } from '../model/model-ctrlr-templ';
import { DataService } from './../data.service';
import { Component, OnInit, EventEmitter, NgZone } from '@angular/core';


@Component({
  selector: 'app-conf-ctrlr-list',
  templateUrl: './conf-ctrlr-list.component.html',
  styleUrls: ['./conf-ctrlr-list.component.css']
})
export class ConfCtrlrListComponent implements OnInit {

  constructor(private ngZone: NgZone, private router: Router, private dataService: DataService) { }
  ctrlrConfListVws: CtrlrConfListVw[]; //The rowData
  delCount: number = 0;
  columnDefs = [
    {
      headerName: 'Tag Name', field: 'ctrlrTagName', resizable: true, sortable: true, cellStyle: { 'display': 'flex', 'align-items': 'center' }, flex: 1,
    },
    {
      headerName: 'Description', field: 'description', resizable: true, sortable: true, cellStyle: { 'display': 'flex', 'align-items': 'center' }, flex: 3,
    },
    {
      headerName: 'CVs', field: 'nCV', sortable: true, resizable: true, cellStyle: { 'display': 'flex', 'align-items': 'center' }, flex: 1,
    },
    {
      headerName: 'MVs', field: 'nMV', sortable: true, resizable: true, cellStyle: { 'display': 'flex', 'align-items': 'center' }, flex: 1,
    },
    {
      headerName: 'DVs', field: 'nDV', sortable: true, resizable: true, cellStyle: { 'display': 'flex', 'align-items': 'center' }, flex: 1,
    },
    {
      headerName: 'Edit', cellRendererFramework: ButtonRendererComponent, width: 60,
      cellStyle: { 'display': 'flex', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        onClick: this.editBtnClick.bind(this),
        label: '',
        iconClass: 'fa fa-edit',
        btnClass: 'btn btn-outline-primary border-0 py-0 px-1 '
      }
    },
    {
      headerName: 'Delete', field: 'delete', editable: false, width: 70, sortable: true, cellRendererFramework: CheckBoxRendererComponent,
      cellStyle: { 'display': 'flex', 'align-items': 'center', 'justify-content': 'center' }, headerClass: "op-header-center",
      cellRendererParams: {
        checkBoxClass: 'big-checkbox',
        onChange: this.deleteCBClick.bind(this),
      }
    }
  ];

  ngOnInit() {
    if (!DemoData.initialized) DemoData.initialize();
  }


  onGridReady(params) {
    console.log('Loading...');
    this.centreMsg.showProcess('Loading...');
    this.ctrlrConfListVws = this.dataService.getCtrlrConfListVw();

    //this.ctrlrTempls = this.dataService.getCtrlrTempls();
    this.centreMsg.stop();

  }


  editBtnClick(e) {
    let temp: CtrlrConf = e.rowData;
    this.ngZone.run(() => this.router.navigateByUrl("configure-controller/" + temp.ctrlrTagName)).then();
    //this.router.navigateByUrl("configure-controller/" + temp.ctrlrTagName)
  }


  deleteCBClick(e) {
    this.deleteCountUpdate();
  }

  private deleteCountUpdate() {
    let delC: number = 0;
    this.ctrlrConfListVws.forEach(e => { if (e.delete) delC++; })
    this.delCount = delC;
  }

  deleteBtnClick(e) {
    this.msgBox.showYesNo('Delete ' + this.delCount + ' controller(s)?', (selected: Selected) => {
      if (selected == Selected.Yes) {
        let ctrlrIdsToDel: string[] = [];
        this.ctrlrConfListVws.forEach(c => {
          if (c.delete) { ctrlrIdsToDel = ctrlrIdsToDel.concat(c.id); };
        });
        this.centreMsg.showProcess('Deleting ' + this.delCount + ' controller(s)...');
        setTimeout(() => {
          this.dataService.deleteCtrlrConf(ctrlrIdsToDel);
          ctrlrIdsToDel.forEach(e => this.ctrlrConfListVws = this.ctrlrConfListVws.filter(i => i.id != e));
          this.deleteCountUpdate();
          this.centreMsg.stop();
        }, 500);
      }
    })
  }

  centreMsg: CentreMsgObject;
  onCMReady(param) {
    this.centreMsg = param;
  }

  msgBox: MsgBoxObj;
  onMBReady(param) {
    this.msgBox = param;
  }



}
