import { Component, OnInit, ElementRef } from '@angular/core';
import * as Highcharts from 'highcharts';

@Component({
  selector: 'app-charttest',
  templateUrl: './charttest.component.html',
  styleUrls: ['./charttest.component.css']
})
export class CharttestComponent implements OnInit {
  highcharts: typeof Highcharts = Highcharts; // required
  chartConstructor: string = 'chart'; // optional string, defaults to 'chart'
  options: Highcharts.Options = {
    chart: {
      pinchType: 'xy',
      zoomType: 'xy',
      animation: false
    },
    plotOptions: {
      line: {
        marker: {
          enabled: false
        }
      }
    },
    tooltip: {
      enabled: false,
      //animation: true
    }
  };
  chartCallback: Highcharts.ChartCallbackFunction = null //function (chart) { } // optional function, defaults to null
  updateFlag: boolean = false; // optional boolean
  oneToOneFlag: boolean = false; // optional boolean, defaults to false
  runOutsideAngular: boolean = false; // optional boolean, defaults to false

  constructor() {
  }

  val: number = 0;

  ngOnInit() {

    this.options.series = [];
    this.options.series = this.options.series.concat({
      data: [],
      type: 'line'
    });
    var Series: any;
    Series = this.options.series[0];

    for (let n = 0; n < 50000; n++) {
      this.val = this.val + (Math.random() * 10 - 5);
      Series.data = Series.data.concat(this.val);
    }
    this.updateFlag = true;

    // setInterval(() => {
    //   this.val = this.val + (Math.random() * 10 - 5);
    //   Series.data = Series.data.concat(this.val);
    //   this.updateFlag = true;
    // }, 25)

  }

}
