import { DataService } from './../../data.service';
import { DemoData } from './../../demo';
import { MapPair } from './../../model/model-ctrlr-templ';
import { CtrlrTempl, CtrlrTemplVw, Mapping } from '../../model/model-ctrlr-templ';
import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { HostListener } from '@angular/core';
import { MsgBoxObj, CentreMsgObject, Selected } from 'open-plant-ng';

@Component({
  selector: 'app-tabgrid',
  templateUrl: './tabgrid.component.html',
  styleUrls: ['./tabgrid.component.css']
})
export class TabgridComponent implements OnInit {

  constructor(private dataService: DataService) { }

  @Input() ctrlrTempl: CtrlrTempl;
  @Input() centreMsg: CentreMsgObject;
  @Input() msgBox: MsgBoxObj;
  selectedMapping: Mapping;
  showCP: boolean = false;
  //@Output() ctrlrTemplChange: EventEmitter<CtrlrTempl> = new EventEmitter<CtrlrTempl>();

  ctrlrTemplVw: CtrlrTemplVw
  ngOnInit() {
    this.ctrlrTemplVw = this.ctrlrTempl.genCtrlrTemplConfVw();
    if (this.ctrlrTemplVw.mappings.length > 0) this.selectedMapping = this.ctrlrTemplVw.mappings[0]; else this.selectedMapping = null;
  }

  evalMappingDisabled() {
    return (this.selectedMapping == null || !this.selectedMapping.canDelete);
  }

  evaluateActiveMapping(mapping: Mapping) {
    if (this.selectedMapping == null) return false;
    return this.selectedMapping.id == mapping.id;
  }

  newMapping() {
    let newMapping = new Mapping(null, "New Mapping " + (this.ctrlrTemplVw.mappings.length + 1), [], true);
    this.ctrlrTemplVw.mappings = this.ctrlrTemplVw.mappings.concat(newMapping);
    this.selectedMapping = newMapping;
  }

  deleteMapping() {
    this.ctrlrTemplVw.mappings = this.ctrlrTemplVw.mappings.filter(m => m.id != this.selectedMapping.id);
    this.selectedMapping = null;
  }

  newMapPair() {
    this.selectedMapping.mapPairs = this.selectedMapping.mapPairs.concat(new MapPair(this.selectedMapping.mapPairs.length, "", ""));
  }

  deleteMapPair(mapPair: MapPair) {
    this.selectedMapping.mapPairs = this.selectedMapping.mapPairs.filter(mp => mp.text != mapPair.text);
  }

  saveAllClick() {
    this.centreMsg.showProcess("Saving...");
    setTimeout(() => {
      let ctrlrTempl: CtrlrTempl = this.ctrlrTemplVw.toCtrlrTempl();
      this.dataService.saveCtrlrTempl(ctrlrTempl);
      console.log(DemoData.ctrlrTempls[0])
      this.centreMsg.stop();
    }, 500);

  }

  private selectedColor: string;
  colorChanged(event, mapPair: MapPair) {
    this.selectedColor = event.color.hex;
  }

  private shownMapPair: MapPair;
  showColorPicker(mapPair: MapPair) {
    mapPair.showCP = true;
    this.shownMapPair = mapPair;
  }

  okCPClick(mapPair: MapPair) {
    mapPair.colorHex = this.selectedColor;
    mapPair.showCP = false;
  }

  cancelCPClick(mapPair: MapPair) {
    mapPair.showCP = false;
    this.shownMapPair = null;

  }
  onInputKeyDown(event, mapPair: MapPair) {
    //if (event.key == "Enter") this.onInputTextOKClick();
    //console.log(mapPair);
    if (event.key == "Escape") this.cancelCPClick(mapPair);
  }

  @HostListener('document:keydown.escape', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    if (this.shownMapPair != null) {
      if (event.key == "Escape") this.cancelCPClick(this.shownMapPair);
    }
  }
}
