import { CtrlrConfRowCol } from './model-ctrlr-conf';
import { FetchItem, OPFetch } from './../global';
import { Mapping, CtrlrTemplParam } from './model-ctrlr-templ';
import { CtrlrConf } from './../model/model-ctrlr-conf';
import { CtrlrVarType, DType, NumberFormat } from './enums';
import * as moment from 'moment';


export class CtrlrHomeVw {
    constructor(ctrlrConf: CtrlrConf) {
        this.id = ctrlrConf.id;
        this.ctrlrTagName = ctrlrConf.ctrlrTagName;
        this.description = ctrlrConf.description;
        this.nCV = ctrlrConf.ctrlrConfTabs.find(t => t.tabId == CtrlrVarType.CV).rows.length;
        this.nMV = ctrlrConf.ctrlrConfTabs.find(t => t.tabId == CtrlrVarType.MV).rows.length;
        this.nDV = ctrlrConf.ctrlrConfTabs.find(t => t.tabId == CtrlrVarType.DV).rows.length;
        this.nINF = ctrlrConf.ctrlrConfTabs.find(t => t.tabId == CtrlrVarType.INF).rows.length;

        this.modeMapping = ctrlrConf.getCtrlrRowCol("Mode").ctrlrTemplParam.mapping;
        this.mode = "";
        let Temp: CtrlrConfRowCol = ctrlrConf.mainParams.find(mp => mp.ctrlrTemplParam.name == "Mode");
        if (Temp != null) {
            if (Temp.isOPTag) this.modeTagId = Temp.value; else {
                this.modeTagId = null;
                this.mode = Temp.value;
            }
        }

        this.statusMapping = ctrlrConf.getCtrlrRowCol("Status").ctrlrTemplParam.mapping;
        this.status = "";
        let Temp2: CtrlrConfRowCol = ctrlrConf.mainParams.find(mp => mp.ctrlrTemplParam.name == "Status");
        if (Temp2 != null) {
            if (Temp2.isOPTag) this.statusTagId = Temp2.value; else {
                this.statusTagId = null;
                this.status = Temp2.value;
            }
        }
        this.ctrlrConf = ctrlrConf;
    }
    id: string;
    ctrlrTagName: string;
    description: string;

    mode: string;
    modeNumber: number;
    modeMapping: Mapping;
    modeTagId: string;


    status: string;
    statusNumber: number;
    statusMapping: Mapping;
    statusTagId: string;

    nCV: number;
    nMV: number;
    nDV: number
    nINF: number;
    Details: string;
    private ctrlrConf: CtrlrConf;

    // private getFetchItems(): FetchItem[] {
    //     let fetchItems: FetchItem[] = [];
    //     this.ctrlrConf.mainParams.forEach(mp => {
    //         if (mp.isOPTag) fetchItems = fetchItems.concat(new FetchItem(mp.value, mp.ctrlrTemplParam.name));
    //         else this.status = mp.value;
    //     });
    //     return fetchItems;
    // }


    update(opFetch: OPFetch) {
        if (this.statusTagId != null) {
            this.statusNumber = opFetch.getResult(this.statusTagId);
            if (this.statusMapping == null) this.status = this.statusNumber.toString();
            else this.status = this.statusMapping.getValueText(this.statusNumber);
        }

        if (this.modeTagId != null) {
            this.modeNumber = opFetch.getResult(this.modeTagId);
            if (this.modeMapping == null) this.mode = this.modeNumber.toString();
            else this.mode = this.modeMapping.getValueText(this.modeNumber);
        }
    }
}

export class CtrlrXVKPIVw {
    constructor(name: string, tabLabel: string, ctrlrVarType: CtrlrVarType, headerTopRow: HeaderCell[], headerBotRow: string[], dataRows: DataRow[]) {
        this.name = name;
        this.tabLabel = tabLabel;
        this.ctrlrVarType = ctrlrVarType;
        this.headerTopRow = headerTopRow;
        this.headerBotRow = headerBotRow;
        this.dataRows = dataRows;
    }
    name: string;
    ctrlrVarType: CtrlrVarType;
    tabLabel: string;
    headerTopRow: HeaderCell[];
    headerBotRow: string[];
    dataRows: DataRow[];
}

//This is for view
export class HeaderCell {
    constructor(rowSpan: number, colSpan: number, header: string) {
        this.rowSpan = rowSpan;
        this.colSpan = colSpan;
        this.header = header;
    }
    rowSpan: number;
    colSpan: number;
    header: string;
}

//This is for view
export class DataRow {
    constructor(dataCells: DataCell[]) {
        this.dataCells = dataCells;
    }
    dataCells: DataCell[];
}

//This is for view
export class DataCell {
    constructor(value: any, tagId: string, color: string, ctrlrTemplParam: CtrlrTemplParam) {
        this.value = value;
        this.tagId = tagId;
        this.color = color;
        this.ctrlrTemplParam = ctrlrTemplParam;
        this.setValue(value)
    }
    value: any;
    valueStr: string;
    tagId: string; //For OPFetch, if null keep
    color: string;
    ctrlrTemplParam: CtrlrTemplParam;

    setValue(valueIn: any) {
        this.value = valueIn;
        if (this.ctrlrTemplParam.dType == DType.string) {
            this.valueStr = this.value;
        } else {
            switch (this.ctrlrTemplParam.numberFormat) {
                case NumberFormat.TwoDecimals: this.valueStr = (Math.round(this.value * 100) / 100).toFixed(2).toString(); break;
                case NumberFormat.Percentage: this.valueStr = (Math.round(this.value * 10) / 10).toFixed(1).toString() + '%'; break;
                case NumberFormat.None:
                    if (this.ctrlrTemplParam.mapping == null) this.valueStr = valueIn; else {
                        let mp = this.ctrlrTemplParam.mapping.mapPairs.find(mp => mp.val == valueIn);
                        if (mp != null) {
                            this.valueStr = mp.text;
                            this.color = mp.colorHex;
                        } else this.valueStr = valueIn;

                    }

                    break;
            }
        }
    }

}

